@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap");

$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #f6f8fa;

.testDetail_page {
  width: 100%;
  height: 100%;
  
  .textarea {
    width: 100%;
    height: fit-content;
    padding: 0px 0px;

    textarea {
      box-shadow:
        #dae1e9 0px 0px 1px 0px,
        #dae1e9 0px 0px 2px 0px;
      width: 100%;
      min-height: 100px;
      border-radius: 3px;
      padding: 5px;
      color: #485567;
      font-size: 0.8rem;
      border: 1px solid #dae1e9;
    }
  }
  textarea:focus {
    border: 1px solid rgb(93, 114, 144);
    box-shadow:
      rgb(93, 114, 144) 0px 0px 2px 0px,
      rgb(93, 114, 144) 0px 0px 1px 0px;
    outline: none;
  }

  overflow: hidden;
  .test_detail {
    width: 100%;
    height: 100%;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    background-color: $background_shade;
    background-color: #ebeef2;
    .inner_testdetail {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      // flex-direction: column;
      // padding: 10px;
      // gap: 10px;
      .testcase-info-box {
        width: 25%;
        height: 100%;
        padding: 10px;
        .test_case_name-and-info {
          position: relative;
          width: 100%;
          height: 100%;
          background-color: white;

          box-shadow:
            0 2px 2px #dae1e9,
            1px 2px 2px #dae1e9;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
          // box-shadow: 0px 2px 4px #3f4d67;
          border-radius: 3px;

          .testcase-name-status {
            width: 100%;
            height: fit-content;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 10px;
            padding: 5px 15px 15px 15px;
            border-bottom: 1.5px solid #dae1e9;

            .navigate-edit {
              // border: 2px solid red;
              width: 100%;
              display: flex;
              justify-content: space-between;
              align-items: center;
              p {
                font-size: 0.9vw;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 3px;
                color: rgb(93, 114, 144);
                cursor: pointer;
                font-weight: 400;
              }
            }

            .name-id {
              width: 100%;
              margin-bottom: 20px;

              h2 {
                font-size: 1.3vw;
                font-weight: 500;
                color: #455264;
                font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
                word-wrap: break-word; /* Break long words if needed */
                white-space: normal;
              }
            }
            .st {
              width: 100%;
              // border: 2px solid red;
              display: flex;
              justify-content: flex-start;
              align-items: center;

              p {
                font-size: 0.9vw;
              }
            }
          }

          .description {
            width: 100%;
            height: fit-content;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 10px;
            padding: 15px;
            // border-bottom: 1.5px solid #dae1e9;

            .name {
              width: 100%;
              color: #3f4d67;
              font-size: 1vw;
              font-weight: 500;
            }

            .value {
              width: 100%;
              color: #485567;
              font-size: 0.5vw;
            }

            .people {
              width: 100%;
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
              gap: 10px;

              p {
                font-size: 1vw;
              }

              .role-section {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 10px;
                flex-direction: column;

                .stp {
                  width: 100%;
                  display: flex;
                  justify-content: flex-start;
                  align-items: center;
                  // flex-direction: column;
                  // border: 2px solid red;

                  p {
                  }
                }
              }
            }
          }
        }
      }
      .tab-and-outlet {
        width: 75%;
        height: 100%;
        // border: 2px solid red;
        padding-right: 15px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        gap: 7px;
        // padding: 15px 15px 15px 0px;

        padding: 10px 0px;

        .tab_for_test_case {
          width: 100%;

          height: 35px;
          display: flex;
          justify-content: flex-start;
          align-items: flex-end;
          border-bottom: 1.5px solid #d2dbe4;

          // padding: 15px 0px;
          .tab {
            width: fit-content;
            height: 30px;
            background-color: #ebf0f5;
            border-top: 1px solid #d2dbe4;
            border-left: 1px solid #d2dbe4;
            border-right: 1px solid #d2dbe4;

            // border: 2px solid red;
            p {
              width: 100%;
              height: 100%;
              font-size: 0.7rem;
              display: flex;
              justify-content: center;
              align-items: center;

              .links {
                text-decoration: none;
                width: 100%;
                height: 100%;
                padding: 4px 15px;
                display: inline-block;
                color: #536079;
                display: flex;
                justify-content: center;
                align-items: center;
              }

              .active {
                width: 100%;
                height: 100%;
                border-radius: 2px 2px 0px 0px;
                border-top: 1.5px solid #2f63c7;
                border-left: 1.5px solid #2f63c7;
                border-right: 1.5px solid #2f63c7;
                border-bottom: 1.5px solid #2f63c7;
                color: #455264;
                font-weight: 500;
                padding: 4px 8px;
              }
            }
          }
        }

        .display {
          // display: none;
          width: 100%;
          height: calc(100% - 42px);
          // border: 2px solid red;
          overflow-y: auto;
          background-color: white;
          padding: 10px;
          box-shadow:
            0 2px 2px #dae1e9,
            1px 2px 2px #dae1e9;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
          border-radius: 3px;
        }
      }
    }
  }
}


.display::-webkit-scrollbar{
  width: 5px;
}

.display::-webkit-scrollbar-track {
  background: #f1f1f1; 
  border-radius: 50px;
}
 
/* Handle */
.display::-webkit-scrollbar-thumb {
  background: #cccbcb; 
  border-radius: 50px;
}

/* Handle on hover */
.display::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

@media screen and (min-width: 2560px) {
  .testDetail_page {
    .test_detail {
    }
  }
}

@media screen and (max-width: 2559px) and (min-width: 1920px) {
  .testDetail_page {
    .test_detail {
      .inner_testdetail {
        .test_case_name-and-info {
          height: 100%;
          .testcase-name-status {
            .st {
              p {
                font-size: 0.9rem;
              }
            }
          }
        }
        .tab-and-outlet {
          // border: 2px solid red;
          .tab_for_test_case {
            height: 80px;
            .tab {
              height: 50px;

              p {
                font-size: 1rem;

                .links {
                  padding: 7px 15px;
                }
              }
            }
          }

          .display {
            height: calc(100% - 80px);
          }
        }
      }
    }
  }
}